<template>
	<div id="dailydetails-ranking">
		<!--顶部菜单-->
		<nav-bar class="dailydetails-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				每日详情
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
		<!--顶部菜单客户页面也用到所以抽出来-->
		<ranking-top-navs/>
		<!--数量和金额客户页面也用到所以抽出来-->
		<ranking-filter-navs :test="fiterArr" @itemClcik="rankingClcik"/>
		<!--滚动--->
		<scroll class="dailydetails-ranking-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
		<!--排行列表-->
		<ranking-content :productArr="cproductkingArr"/>
		</scroll>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import Scroll from 'components/common/scroll/Scroll'
import RankingTopNavs from 'components/contents/rankingnavs/RankingTopNavs'
import RankingFilterNavs from 'components/contents/rankingnavs/RankingFilterNavs'
import RankingContent from './childComps/RankingContent'


export default {
  name: 'ProductRanking',
	components: {
		NavBar,
		Scroll,
		RankingTopNavs,
		RankingFilterNavs,
		RankingContent
	},
	data(){
		return{
			fiterArr:["","","数量","金额"],
			productkingArr:[],
			sortType:'num',				//当前选中排序类型
		}
	},
	methods:{
		//返回按钮
		backClick(){
			//this.$router.replace("/customersdetail").catch(res=>{res})
			this.$router.go(-1)
		},
		//加载更多
		loadMove(){
			console.log("加载更多")
		},
		//数量和金额点击
		rankingClcik(index){
			switch(index){
				case 2:
					this.sortType="num"
					break
				case 3:
					this.sortType="price"
					break
			}
		},
		//排序-需要抽到工具类
		compare (property){
			return function(a,b){
				let value1=a[property]
				let value2=b[property]
				return value2-value1
			}
		},
	},
	mounted(){
		this.productkingArr=this.$store.getters.productkingArr
	},
	computed:{
		cproductkingArr(){
			//排序
			let arr=this.productkingArr
			return arr.sort(this.compare(this.sortType))
		}
	}
}
</script >

